@import './../Common/Styles/Sass/variabless';
@import './../Common/Styles/Sass/mixins';

.header {
    @include flex(row, center, center, wrap);
    position: fixed;
    height: 50px;
    min-width: 100%;
    background-color: $bgSecondColor;
    width: 100%;
    z-index: 10;
    @media screen and (max-width:1030px){
        height: 60px;
    }
}